@import '../variables';

.sort {
  position: relative;
  padding-right: 20px;
  margin-left: 50px;

  @media (max-width:1260px) {
    margin-left: 0;
  }

  &__label {
    display: flex;
    align-items: center;

    svg {
      margin-right: 8px;
    }

    b {
      margin-right: 8px;
    }

    span {
      color: $orange;
      border-bottom: 1px dashed $orange;
      cursor: pointer;
    }
  }

  &__popup {
    width: 300px;
    height: 250px;
    position: absolute;
    top: 20px;
    right: 0;
    margin: 15px 20px;
    background: #ffffff;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.09);
    border-radius: 10px;
    padding: 10px 0;
    overflow: scroll;

    ul {
      overflow: hidden;
      padding: 0;
      li {
        padding: 12px 20px;
        cursor: pointer;

        &.active,
        &:hover {
          background: rgba(254, 95, 30, 0.05);
        }

        &.active {
          font-weight: bold;
          color: $orange;
        }
      }
    }
  }
}
