@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;
  a {
    text-decoration: none;
  }

  .header-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 0 40px;

    @media (max-width: 860px) {
      flex-direction: column;

    }
  }

  &__cart {
    @media (max-width: 860px) {
      margin-top: 20px;
    }
  }

  &__logo {
    display: flex;

    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: normal;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }
  }
}
